interface ElementConfig {
  src: string;
  x: number;
  y: number;
  width: number;
  height: number;
}

interface Lines {
  _1: string;
  _2: string;
  _3: string;
  _4: string;
  x0: number;
  y0: number;
  fontSize: {
    default: number;
    distance: number;
  };
}

interface Portraits {
  size: number;
  x0: number;
  y0: number;
  delta: {
    x: number;
    y: number;
  };
}

interface TallConfig {
  background: {
    src: string;
  };
  stickers: {
    _1: ElementConfig;
    _2: ElementConfig;
    _3: ElementConfig;
  };
  sda: ElementConfig;
}

interface SquareConfig {
  background: {
    src: string;
  };
  sda: ElementConfig;
}

