function grayscale(
  ctx: any,
  x0: number,
  y0: number,
  width: number,
  height: number
) {
  const imageData = ctx.getImageData(x0 / 2, y0 / 2, width / 2, height / 2);

  // This loop gets every pixels on the image and
  for (let j = 0; j < imageData.height; j++) {
    for (let i = 0; i < imageData.width; i++) {
      const index = i * 4 * imageData.width + j * 4;
      const red = imageData.data[index];
      const green = imageData.data[index + 1];
      const blue = imageData.data[index + 2];
      const alpha = imageData.data[index + 3];
      //BT.601 grayscale conversion
      const gray = red * 0.299 + green * 0.587 + blue * 0.114;
      imageData.data[index] = gray;
      imageData.data[index + 1] = gray;
      imageData.data[index + 2] = gray;
      imageData.data[index + 3] = alpha;
    }
  }
  ctx.putImageData(imageData, x0 / 2, y0 / 2);
}

function getCanvas(width: number, height: number, scale: number, id: string) {
  const canvas: any = document.getElementById(id);
  canvas.style.width = width;
  canvas.style.height = height;
  canvas.width = width * scale;
  canvas.height = height * scale;
  return canvas;
}

function drawLines(ctx: any, canvas: any, lines: Lines) {
  ctx.font = lines.fontSize.default + "px Michroma";
  ctx.strokeStyle = "#003300";
  ctx.textBaseline = "top";
  ctx.textAlign = "left";

  const lineX = lines.x0;
  const line1Y = lines.y0;
  const lineSpacingRatio = 14 / 13;
  const line2Y = line1Y + lines.fontSize.default * lineSpacingRatio;
  const line3Y = line2Y + lines.fontSize.default * lineSpacingRatio;
  const line4Y = line3Y + lines.fontSize.default * lineSpacingRatio;
  ctx.fillText(lines._1, lineX, line1Y);
  ctx.fillText(lines._2, lineX, line2Y);
  ctx.fillText(lines._3, lineX, line3Y);

  const socialDistanceSize = lines.fontSize.distance;
  ctx.font = socialDistanceSize + "px Michroma";
  ctx.lineWidth = 4;
  ctx.strokeText(lines._4, lineX, line4Y);

  ctx.fillStyle = "white";
  ctx.fillRect(2282, 0, canvas.width, line3Y);
  ctx.fillStyle = "black";
}

function mountPortraits(ctx: any, p: Portraits) {
  const x1 = p.x0 + p.delta.x;
  const y1 = p.y0 + p.delta.y;
  const myImage = document.getElementById("my-image");
  ctx.drawImage(myImage, p.x0, p.y0, p.size, p.size);
  grayscale(ctx, p.x0, p.y0, p.size, p.size);

  const theirImage = document.getElementById("their-image");
  ctx.drawImage(theirImage, x1, y1, p.size, p.size);
  grayscale(ctx, x1, y1, p.size, p.size);

  ctx.fillStyle = "rgba(255,255,255,0.25)";
  ctx.fillRect(p.x0, p.y0, p.size, p.size);
  ctx.fillRect(x1, y1, p.size, p.size);
}

// TALL TEMPLATE

function drawTall(lines: Lines, portraits: Portraits, config: TallConfig) {
  const height = 1920 * 2;
  const width = 1080 * 2;
  const scale = 0.5; // TEMP: window.devicePixelRatio
  const canvas = getCanvas(width, height, scale, "tall-canvas");
  const ctx = canvas.getContext("2d");
  ctx.scale(scale, scale);

  // Background gradient
  const background = new Image();
  background.src = config.background.src;
  background.onload = () => {
    ctx.drawImage(background, 0, 0, width, height);
    drawLines(ctx, canvas, lines);
    mountPortraits(ctx, portraits);

    const seal = new Image();
    seal.src = "/template-seal.svg";
    seal.onload = () => {
      const sealSize = 500;
      ctx.drawImage(seal, 1455, 1050, sealSize, sealSize);

      const sticker1 = new Image();
      const sc1 = config.stickers._1;
      sticker1.src = sc1.src;
      sticker1.onload = () => {
        ctx.drawImage(sticker1, sc1.x, sc1.y, sc1.width, sc1.height);

        const sticker2 = new Image();
        const sc2 = config.stickers._2;
        sticker2.src = sc2.src;
        sticker2.onload = () => {
          ctx.drawImage(sticker2, sc2.x, sc2.y, sc2.width, sc2.height);

          const sticker3 = new Image();
          const sc3 = config.stickers._3;
          sticker3.src = sc3.src;
          sticker3.onload = () => {
            ctx.drawImage(sticker3, sc3.x, sc3.y, sc3.width, sc3.height);

            const sda = new Image();
            const sdaC = config.sda;
            sda.src = sdaC.src;
            sda.onload = () => {
              ctx.drawImage(sda, sdaC.x, sdaC.y, sdaC.width, sdaC.height);

              // Finally, generate the image.
              const generatedImage = canvas.toDataURL("image/png");
              const tallImageSlot: any = document.getElementById("tall-image");
              const tallDownloadLink: any = document.getElementById(
                "tall-download-link"
              );

              tallImageSlot.src = generatedImage;
              tallDownloadLink.download = "keep-your-distance-club-story.png";
              tallDownloadLink.href = generatedImage;
            };
          };
        };
      };
    };
  };
}

// SQUARE TEMPLATE

function drawSquare(lines: Lines, portraits: Portraits, config: SquareConfig) {
  const height = 1200 * 2;
  const width = height;
  const scale = 0.5; // TEMP: window.devicePixelRatio
  const canvas = getCanvas(width, height, scale, "square-canvas");
  const ctx = canvas.getContext("2d");
  ctx.scale(scale, scale);

  // Background gradient
  const background = new Image();
  background.src = config.background.src;
  background.onload = () => {
    ctx.drawImage(background, 0, 0, width, height);
    drawLines(ctx, canvas, lines);
    mountPortraits(ctx, portraits);

    const seal = new Image();
    seal.src = "/template-seal.svg";
    seal.onload = () => {
      const sealSize = 405;
      ctx.drawImage(seal, 1160, 1710, sealSize, sealSize);

      const sda = new Image();
      const sdaC = config.sda;
      sda.src = sdaC.src;
      sda.onload = () => {
        ctx.drawImage(sda, sdaC.x, sdaC.y, sdaC.width, sdaC.height);

        const generatedImage = canvas.toDataURL("image/png");

        const squareImageSlot: any = document.getElementById("square-image");
        const squareDownloadLink: any = document.getElementById(
          "square-download-link"
        );

        squareImageSlot.src = generatedImage;
        squareDownloadLink.download = "keep-your-distance-club-square.png";
        squareDownloadLink.href = generatedImage;
      };
    };
  };
}

export default function drawOnCanvas(
  myName: string,
  theirName: string,
  socialDistance: number
) {
  let squareConfig;
  let tallConfig;

  if (socialDistance <= 1 || isNaN(socialDistance)) {
    // Distance must never be 0! ;) or NaN...
    socialDistance = 1;

    squareConfig = squareConfigV3;
    tallConfig = tallConfigV3;
  } else {
    // XXX: Poor man's random.
    if (socialDistance % 2 == 0) {
      squareConfig = squareConfigV1;
      tallConfig = tallConfigV1;
    } else {
      squareConfig = squareConfigV2;
      tallConfig = tallConfigV2;
    }
  }

  const line1 = `${myName}`.toUpperCase();
  const line2 = `+ ${theirName}`.toUpperCase();
  const line3 = "ARE:";
  // Pad with leading zeros so that we always have 8 digits.
  const line4 = ("0000000" + socialDistance)
    .split("")
    .reverse()
    .join("")
    .substr(0, 8)
    .split("")
    .reverse()
    .join("");

  const squarePortraitSize = 554;
  const squarePortraits: Portraits = {
    size: squarePortraitSize,
    x0: 171,
    y0: 1424,
    delta: {
      x: 58 + squarePortraitSize,
      y: 0
    }
  };

  const tallPortraitSize = 610;
  const tallPortraits: Portraits = {
    size: tallPortraitSize,
    x0: 425,
    y0: 1070,
    delta: {
      x: tallPortraitSize + 95,
      y: -tallPortraitSize + 160
    }
  };

  drawSquare(
    {
      _1: line1,
      _2: line2,
      _3: line3,
      _4: line4,
      x0: 100,
      y0: 100,
      fontSize: {
        default: 240,
        distance: 230
      }
    },
    squarePortraits,
    squareConfig
  );
  drawTall(
    {
      _1: line1,
      _2: line2,
      _3: line3,
      _4: line4,
      x0: 125,
      y0: 2188,
      fontSize: {
        default: 220,
        distance: 200
      }
    },
    tallPortraits,
    tallConfig
  );

  return;
}
