<!-- XXX: TEMP: Reenable TypeScript in the future: <script lang="ts"> -->
<script>
import { Component, Vue } from "vue-property-decorator";
import FileUpload from "vue-upload-component";
import VueCropper from "vue-cropperjs";
import "cropperjs/dist/cropper.css";
import Header from "./components/Header.vue";
import FAQ from "./components/FAQ.vue";
import axios from "axios";
import distanceBetweenCoordinates from "./helpers/distanceBetweenCoordinates.ts";
import drawOnCanvas from "./helpers/drawOnCanvas.ts";
import { errorMessage, FormError } from "./helpers/FormError.ts";

@Component({
  components: {
    Header,
    FAQ,
    FileUpload,
    VueCropper
  }
})
export default class App extends Vue {
  data() {
    return {
      document: document,
      AppState: {
        INITIAL: 1,
        FORM_INVALID: 2,
        PROCESSING: 3,
        DONE: 4
      },
      nonBreakableSpace: "\xa0",

      redFireflyActivated: false,
      anyFireflyActivated: true,
      myImage: [],
      theirImage: [],
      apiCallsBlocked: false,
      form: {
        myName: "",
        theirName: "",
        myLocation: "",
        theirLocation: ""
      },
      distanceInfo: {
        myLatitude: "lat",
        myLongitude: "long",
        theirLatitude: "lat",
        theirLongitude: "long"
      },
      showMyCrop: true,
      showTheirCrop: true,
      pauseBanner: false,
      showFAQSection: false,
      skipForm: false,
      currentAppState: 1, // AppState.INITIAL
      longErrorMessage: "",
      shortErrorMessage: ""
    };
  }

  mounted() {
    this.$nextTick(function() {
      setTimeout(() => {
        const el = document.getElementById("loading");
        if (el) {
          el.remove();
        }
        if (window.innerWidth <= 500) {
          document.getElementById("nf-my-location").placeholder =
            "My Location, e.g., Berlin";
          document.getElementById("nf-their-location").placeholder =
            "My Location, e.g., Chicago";
        }
      }, 1000);
    });

    this.skipForm && this.generateImages();
  }

  updateFireflyPosition(e) {
    // Those harcoded 60's here are height of the size firefly image
    // divided by 2.
    this.$refs.firefly.style.top = e.pageY - 60 + "px";
    document.getElementById("firefly").style.left = e.pageX - 60 + "px";
  }

  setFormErrorState(error) {
    this.currentAppState = this.AppState.FORM_INVALID;
    this.longErrorMessage = errorMessage(error, "long");
    this.shortErrorMessage = errorMessage(error, "short");
  }

  generateImages() {
    if (this.skipForm) {
      drawOnCanvas("Tray", "grześ", 44444444);
      //TODO: first make sure images are there, before showing section
      this.currentAppState = this.AppState.DONE;
      return;
    }

    if (
      !this.form.myName ||
      !this.form.myLocation ||
      !this.form.theirName ||
      !this.form.theirLocation
    ) {
      this.setFormErrorState(FormError.FIELD_EMPTY);
      return;
    }

    if (!this.myImage.length) {
      this.setFormErrorState(FormError.MISSING_IMAGE);
      return;
    }

    if (!this.theirImage.length) {
      this.setFormErrorState(FormError.MISSING_IMAGE);
      return;
    }

    this.currentAppState = this.AppState.PROCESSING;
    if (this.apiCallsBlocked) {
      return; //XXX: TEMP: development-only condition
    }

    setTimeout(() => {
      const propagateError = error => {
        if (error === FormError.RESPONSE_EMPTY) {
          this.setFormErrorState(error);
        } else {
          this.setFormErrorState(FormError.UNKNOWN_ERROR);
        }
      };

      axios
        .get(
          `https://nominatim.openstreetmap.org/search?q=${this.form.myLocation}&format=json&limit=1`
        )
        .then(response => {
          if (response.data[0] == null) {
            throw FormError.RESPONSE_EMPTY;
          }
          this.distanceInfo.myLatitude = response.data[0].lat.substring(0, 5);
          this.distanceInfo.myLongitude = response.data[0].lon.substring(0, 5);
          setTimeout(() => {
            axios
              .get(
                `https://nominatim.openstreetmap.org/search?q=${this.form.theirLocation}&format=json&limit=1`
              )
              .then(response => {
                if (response.data[0] == null) {
                  throw FormError.RESPONSE_EMPTY;
                }
                this.distanceInfo.theirLatitude = response.data[0].lat.substring(
                  0,
                  5
                );
                this.distanceInfo.theirLongitude = response.data[0].lon.substring(
                  0,
                  5
                );
                const distance = distanceBetweenCoordinates(
                  this.distanceInfo.myLatitude,
                  this.distanceInfo.myLongitude,
                  this.distanceInfo.theirLatitude,
                  this.distanceInfo.theirLongitude
                );
                drawOnCanvas(this.form.myName, this.form.theirName, distance);
                //TODO: first make sure images are already there, before showing the section
                this.currentAppState = this.AppState.DONE;

                setTimeout(() => {
                  document.getElementById('la-bottom-banner-offset-bar').scrollIntoView({
                    behavior: 'smooth'
                  });
                }, 500);

              })
              .catch(function(error) {
                propagateError(error);
              });
          }, 1000);
        })
        .catch(function(error) {
          propagateError(error);
        });
    }, 1000);
  }

  cropImage(image, cropper) {
    if (cropper == "myCropper") {
      this.showMyCrop = false;
    } else {
      this.showTheirCrop = false;
    }
    image[0].url = this.$refs[cropper].getCroppedCanvas().toDataURL();
  }

  inputMyFile(newFile, oldFile, prevent) {
    if (newFile && !oldFile) {
      this.$nextTick(function() {
        this.showMyCrop = true;
      });
    }
    if (!newFile && oldFile) {
      this.showMyCrop = false;
    }
  }
  inputTheirFile(newFile, oldFile, prevent) {
    if (newFile && !oldFile) {
      this.$nextTick(function() {
        this.showTheirCrop = true;
      });
    }
    if (!newFile && oldFile) {
      this.showTheirCrop = false;
    }
  }

  inputFilter(newFile, oldFile, prevent) {
    if (newFile && !oldFile) {
      if (!/\.(gif|jpg|jpeg|png|webp)$/i.test(newFile.name)) {
        this.alert("Your choice is not a picture");
        return prevent();
      }
    }

    if (newFile && (!oldFile || newFile.file !== oldFile.file)) {
      newFile.url = "";
      const URL = window.URL || window.webkitURL;
      if (URL && URL.createObjectURL) {
        newFile.url = URL.createObjectURL(newFile.file);
      }
    }
  }
}
</script>

