export enum FormError {
  NO_ERROR,
  INVALID_LOCATION,
  INVALID_CHARACTERS,
  FIELD_EMPTY,
  RESPONSE_EMPTY,
  MISSING_IMAGE,
  UNKNOWN_ERROR
}

export function errorMessage(
  error: FormError,
  variant: "long" | "short"
): string {
  switch (error) {
    case FormError.NO_ERROR:
      if (variant === "short") {
        return "";
      } else if (variant === "long") {
        return "";
      }
      break;
    case FormError.INVALID_LOCATION:
      if (variant === "short") {
        return "Invalid location";
      } else if (variant === "long") {
        return "We couldn't measure the distance between these locations. Try again? 🤗";
      }
      break;
    case FormError.INVALID_CHARACTERS:
      if (variant === "short") {
        return "Invalid characters";
      } else if (variant === "long") {
        return "One of the names you entered contains characters we cannot process 😭";
      }
      break;
    case FormError.FIELD_EMPTY:
      if (variant === "short") {
        return "Empty field";
      } else if (variant === "long") {
        return "Please fill out all the text fields!";
      }
      break;
    case FormError.MISSING_IMAGE:
      if (variant === "short") {
        return "Missing image";
      } else if (variant === "long") {
        return "Please upload both images!";
      }
      break;
    case FormError.RESPONSE_EMPTY:
        if (variant === "short") {
            return "City not found";
        } else if (variant === "long") {
            return "We were not able to find one of your locations!";
        }
        break;
    case FormError.UNKNOWN_ERROR:
        if (variant === "short") {
            return "Unknown error";
        } else if (variant === "long") {
            return "Oops, unknown error! 🤭 Refresh?";
        }
        break;
    default:
      if (variant === "short") {
        return "Unknown error";
      } else if (variant === "long") {
        return "Oops, unknown error! 🤭 Refresh?";
      }
  }
  return "";
}
